<script>
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import { Toast } from "../../components/global/swal.js"
import { errorHandle } from "../../utils/handle"
import { apiEmployerLogin, apiCaretakerLogin, apiCheckLineUser, apiLineLogin, apiGetPosition } from "@/api/api"
import { focusInput } from "@/utils/fixFooter"
import { ExtCall, ExtCallThird, ExtCallGPS } from "@/utils/extCall"

export default {
  setup() {
    const router = useRouter()
    const model = process.env.VUE_APP_MODE
    const inputData = ref({ phoneNumber: "", password: "", option: "" })
    let msg = "" //{}
    const form = ref(null)

    // onMounted先判斷驗證 is_Login = 1? 進首頁
    onMounted(() => {
      focusInput()
      if (localStorage.getItem("is_Login") == 1) {
        router.push({ path: "/home" })
      }
    })
    const login = async (e) => {
      e.preventDefault()
      if (form.value.reportValidity()) {
        //post API
        if (process.env.VUE_APP_MODE == "purple") {
          msg = await apiEmployerLogin(inputData.value)
        } else {
          msg = await apiCaretakerLogin(inputData.value)
        }
        loginMsg(msg.status)
      }
    }

    //成功登入alert
    const loginMsg = (val) => {
      if (val == true) {
        Toast("登入成功")
        localStorage.setItem("is_Login", 1)
        router.push({ path: "/home" })
      } else {
        let errMessage = errorHandle(msg)
        Toast(errMessage)
      }
    }


    const backDoor = process.env.VUE_APP_TEST_PASSWORD
    const isBackDoor = ref(false)
    const handleBackDoorOpen = (formData) => {
      const { password, option } = formData
      if (password === backDoor) {
        isBackDoor.value = true
      }
      try {
        if (option === "2") {
          // 切換到測試版本
          const testUrl = process.env.VUE_APP_TEST_V1
          ExtCall.replaceSetting("test", testUrl)
        }
        if (option === "1") {
          // 切換到普通版本
          const defaultUrl = process.env.VUE_APP_STABLE
          ExtCall.replaceSetting("index", defaultUrl)
        }
        if (option === "3") {
          // 切換到Ally版本
          const defaultUrl =
            "http://10.1.1.168/nubk_setting/settings_nubkemployer.txt"
          ExtCall.replaceSetting("index", defaultUrl)
        }
      } catch (error) {
        Toast(`電腦版不能執行 ${error}`)
      }
    }
    let isShowPassword = ref(0)
    const showPassword = () => {
      isShowPassword.value = isShowPassword.value == 0 ? 1 : 0
    }

    // const startGPS = () => {
    //   try {
    //     window.ExtCallStartGPS = () => {return true} // 啟動GPS後呼叫取值function
    //     ExtCallGPS.startGPS("ExtCallStartGPS")
    //   } catch (error) {
    //     alert(error.message)
    //   }
    // }
    // const getCurrentLocation = (GPSstatus) => {
    //   // alert(GPSstatus)
    //   try {
    //     window.ExtCallGetCurrentLocation = async (time, lat, long) => {
    //       alert(`${time +"/"+ lat +"/"+ long}`)
    //       if (typeof (time) === "string" && time?.length > 6) {// 代表都有抓到回傳時間了
    //         const response = await apiGetPosition({ longitude: long, latitude: lat })
    //         console.log('apiGetPosition已發送'+'/'+ response.data.defaultPlace.cityName + '/' + response.data.defaultPlace.townName)
    //         // 存後端回傳轉換的 cityId areaId
    //         // 設值改pinia
    //       }
    //     }
    //     ExtCallGPS.getCurrentLocation("ExtCallGetCurrentLocation")
    //   } catch (error) {
    //     alert(error.message)
    //   }
    // }
    const appleLoginTest = () => {
      let appleInput = "";
      window.appleLogin = (stringObject) => {
        appleInput = JSON.parse(stringObject); //status= ? | ?
        alert((appleInput.token));
      };
      ExtCallThird.appleLogin("appleLogin")
      console.log("appleLoginTest")
    }

    const lineLogin = () => {
      try {
        const channel_id = "1655750341" // 看護 line channel

        window.lineLogin = async (extcall_line) => {
          extcall_line = JSON.parse(extcall_line)
          if (extcall_line.status === "cancel") {
            Toast("失敗!")
            return
          }

          // 打API檢查是否有這個 line user
          // 是 登入
          const response = await apiCheckLineUser({ access_token: extcall_line.token })
          // alert(JSON.stringify(response))
          if (response.status) {
            const loginResponse = await apiLineLogin({ access_token: extcall_line.token })
            loginMsg(loginResponse.status)
            return
          }
          // 否 去註冊
          // router跳轉帶入參數 ?line_access_token=xxx
          if (extcall_line) {
            router.push(`/signup?line_access_token=${extcall_line.token}&line_name=${response.data.name}`)
          }
        }
        ExtCallThird.lineLogin(channel_id, "lineLogin")
      } catch (error) {
        console.log(error.message)
      }
    }

    return {
      inputData,
      login,
      form,
      handleBackDoorOpen,
      isBackDoor,
      backDoor,
      showPassword,
      isShowPassword,
      model,
      // startGPS,
      lineLogin,
      appleLoginTest,
      // getCurrentLocation,
    }
  },
}
</script>

<template>
  <div class="fullScreen">
    <div class="login">
      <div class="title" @click="handleBackDoorOpen(inputData)">會員登入</div>
      <!-- 隱藏選項 -->
      <select v-if="isBackDoor" v-model="inputData.option" class="form-select" aria-label="Default select example">
        <option value="">選擇要切換到哪個版本?</option>
        <option value="1" selected>穩定版</option>
        <option value="2">Demo版</option>
        <!-- <option value="3" v-if="model == 'purple'">工程版</option> -->
      </select>
      <!--  -->
      <div class="login-main">
        <form ref="form">
          <div class="mb-3">
            <input type="text" class="form-control" placeholder="請輸入手機號碼" v-model="inputData.phoneNumber"
              title="請輸入手機號碼" required />
          </div>
          <div class="mb-3 passwordArea">
            <div v-if="isShowPassword == 0">
              <input type="password" class="form-control" placeholder="請輸入密碼" v-model="inputData.password"
                pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required />
              <a class="nav-link" @click="showPassword"><i class="far fa-eye"></i></a>
            </div>
            <div v-if="isShowPassword == 1">
              <input type="text" class="form-control" placeholder="請輸入密碼" v-model="inputData.password"
                pattern="^(?=.*[A-Za-z])(?=.*[0-9]).{6,}$" title="最少6個字元，需有英文及數字" required />
              <a class="nav-link" @click="showPassword"><i class="far fa-eye-slash"></i></a>
            </div>
          </div>
        </form>
        <div class="mt-4b btn-container">
          <div class="row">
            <div class="col">
              <button class="btn btn-primary" type="button" @click.prevent="login">
                登入
              </button>
            </div>
            <div class="col">
              <button class="btn btn-outline-primary" type="button" @click="$router.push({ path: '/signup' })">
                註冊
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="login-main2">
        <div @click="$router.push({ path: '/home' })">訪客登入</div>
        <div>.</div>
        <div @click="$router.push({ path: '/login/forget' })">忘記密碼</div>
      </div>
      <!-- <div class="login-main3">
        <hr>
        <div class="otherLogin">
          <img @click="lineLogin" src="@/assets/image/line_icon.png" alt="">
        </div>
        <hr>
        <div class="otherLogin">
          <p @click="appleLoginTest">apple 登入</p>
        </div>
      </div> -->
      <!-- <div class="login-main2">
        <div @click="startGPS">啟動GPS</div>
        <div>.</div>
        <div @click="lineLogin">呼叫Line登入</div>
        <div>.</div>
        <div @click="getCurrentLocation">GPS取經緯度</div>
      </div> -->
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";

.fullScreen {
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  background-image: url(#{$img}#{nth($login-bg-image, $style)
});
background-repeat: no-repeat;
background-position: bottom;
background-size: 100vw auto;
}

.login {
  margin-top: 3.75rem;
  margin-left: 2rem;
  margin-right: 2rem;

  .title {
    font-size: 1.5rem;
    font-weight: 400;
    margin-bottom: 1rem;
  }

  .login-main {
    .form-control {
      margin: 1.5rem 0;
      color: #909090;
      border-color: #bfbfbf;
      @include placeholder(#909090);

      &:focus {
        color: #909090;
        border-color: $primary-color;
      }

      &[readonly] {
        background-color: #f9f9f9;

        &:focus {
          border-color: #bfbfbf;
        }
      }
    }

    .btn-container {
      padding-top: 15px;

      .row {
        margin-left: 0;
        margin-right: 0;
      }

      .col {
        text-align: center;
      }
    }

    .btn {
      padding: 0.3125rem 0.5rem;
      width: 7.25rem;
      border-radius: 2rem;
    }

    .btn-primary {
      background-color: $primary-color;
      border-color: $primary-color;

      &:active,
      &:hover,
      &:focus {
        background-color: $primary-color;
        border-color: $primary-color;
      }
    }

    .btn-outline-primary {
      color: $primary-color;
      border-color: $primary-color;

      &:active {
        color: $primary-color;
        background-color: white;
        border-color: $primary-color;
      }

      &:hover {
        color: white;
        background-color: $primary-color;
        border-color: $primary-color;
      }
    }
  }

  .login-main2 {
    margin: 1rem auto;
    display: flex;
    justify-content: center;
    color: #909090;

    div {
      margin: 0 0.2rem;
    }
  }

  .login-main3 {

    hr {
      border: none;
      border-top: 1px double #333;
      color: #333;
      overflow: visible;
      text-align: center;
      height: 1px;
    }

    hr:after {
      background: #fff;
      content: '其他登入方式';
      padding: 0 4px;
      position: relative;
      top: -13px;
    }

    .otherLogin {
      display: flex;
      justify-content: center;
    }
  }
}

.passwordArea {
  position: relative;

  a {
    position: absolute;
    top: 0rem;
    right: 0rem;
    color: #909090;
  }
}
</style>
